.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
    @media (max-width: 767px) {
      .modal__wrapper {
        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 500px;
    min-height: 610px;
    margin-top: 60px;
    margin-right: 16px;
    padding: 40px 40px 20px;
    background-color: #00b2ff;
    border-radius: 3px; }
    @media (max-width: 767px) {
      .modal__content {
        width: 100%;
        margin: 0;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 1;
    width: 40px;
    height: 40px;
    padding: 0;
    background: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer; }
  .modal__close-icon {
    fill: #ffffff; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    transition: none; }
  .modal.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

