.contact {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(225px, auto));
  column-gap: 30px;
  row-gap: 10px;

  @include vp-1023 {
    grid-template-columns: repeat(auto-fit, minmax(40px, auto));
    justify-content: end;
    column-gap: 20px;
  }
}

.contact__item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 15px;
  padding-left: 35px;
  font-size: 16px;
  line-height: 1.13;
  color: $color-alice-blue;

  &:hover {
    color: $color-deep-sky-blue;
  }

  &:focus {
    outline: auto;
  }

  &:focus-visible {
    color: $color-deep-sky-blue;
    outline: 1px solid $color-deep-sky-blue;
  }

  &:active {
    color: $color-silver;
    outline: none;
  }

  @include vp-1023 {
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}

.contact__item:active .contact__icon {
  fill: $color-silver;
}

.contact__item span {
  @include vp-1023 {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }
}

.contact__icon {
  position: absolute;
  top: 50%;
  left: 0;
  flex: 0 0 auto;
  fill: $color-deep-sky-blue;
  transform: translateY(-50%);

  @include vp-1023 {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    transform: none;
  }
}
